import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController } from 'ionic-angular';

/**
 * Generated class for the IpcContactHistoryPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-ipc-contact-history',
  templateUrl: 'ipc-contact-history.html',
})
export class IpcContactHistoryPage {

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public modalCtrl: ModalController
  ) {

  }

  showModal(page) {
    let modal = this.modalCtrl.create(page,{
      // userdata : this.userdata,
      // dataAbsen: absen ,
      // jenisAbsen: '2'       
     }, {
       enableBackdropDismiss: true,
       showBackdrop:true,
       cssClass:'my-modal2'
     });
     modal.present();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad IpcContactHistoryPage');
  }

}



